import React, { Component } from 'react'
import {
  Button,
  Progress,
  UncontrolledDropdown,
  DropdownMenu,
  DropdownToggle,
  DropdownItem,
  Input
} from 'reactstrap'
import { toast } from 'react-toastify'
import DataTable from 'react-data-table-component'
import classnames from 'classnames'
import ReactPaginate from 'react-paginate'
import { history } from '../../history'
import { ToastContainer } from 'react-toastify'
import {
  Edit,
  Info,
  Trash,
  ChevronDown,
  Plus,
  Check,
  ChevronLeft,
  ChevronRight
} from 'react-feather'
import { connect } from 'react-redux'
import {
  getSpendData,
  getSpendInitialData,
  deleteData,
  updateData,
  addData,
  filterData
} from '../../redux/actions/data-list'
import 'react-toastify/dist/ReactToastify.css'
import '../../assets/scss/plugins/extensions/toastr.scss'
import Sidebar from './DataListSidebar'
import axios from 'axios'
import Chip from '../../components/@vuexy/chips/ChipComponent'
import Checkbox from '../../components/@vuexy/checkbox/CheckboxesVuexy'

import '../../assets/scss/plugins/extensions/react-paginate.scss'
import '../../assets/scss/pages/data-list.scss'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";

const chipColors = {
  'on hold': 'warning',
  delivered: 'success',
  pending: 'primary',
  canceled: 'danger'
}

const selectedStyle = {
  rows: {
    selectedHighlighStyle: {
      backgroundColor: 'rgba(115,103,240,.05)',
      color: '#7367F0 !important',
      boxShadow: '0 0 1px 0 #7367F0 !important',
      '&:hover': {
        transform: 'translateY(0px) !important'
      }
    }
  }
}
const EditComponent = (props) => {
  return (
    <div className="data-list-action">
      {window.screen.width > 600 ? (
        <Edit
          className="cursor-pointer mr-1"
          size={window.screen.width < 600 ? 13 : 20}
          onClick={() => {
            return props.currentData(props.row)
          }}
        />
      ) : (
        <Info
          className="cursor-pointer mr-1"
          size={window.screen.width < 600 ? 13 : 20}
          onClick={() => {
            return props.currentData(props.row)
          }}
        />
      )}
    </div>
  )
}
const DeleteComponent = (props) => {
  return (
    <div className="data-list-action">
      <Trash
        className="cursor-pointer"
        size={window.screen.width < 600 ? 13 : 20}
        onClick={() => {
          props.deleteRow(props.row)
        }}
      />
    </div>
  )
}
const ActionsComponent = (props) => {
  return (
    <div className="data-list-action">
      <Edit
        className="cursor-pointer mr-1"
        size={window.screen.width < 600 ? 13 : 20}
        onClick={() => {
          return props.currentData(props.row)
        }}
      />
      {window.screen.width > 600 && (
        <Trash
          className="cursor-pointer"
          size={window.screen.width < 600 ? 13 : 20}
          onClick={() => {
            props.deleteRow(props.row)
          }}
        />
      )}
    </div>
  )
}

const CustomHeader = (props) => {
  return (
    <div className="data-list-header d-flex justify-content-between flex-wrap">
      <div className="actions-left d-flex flex-wrap">
        <DropdownToggle
          color="white"
          className="sort-dropdown mx-50"
          style={{
            height: '75%',
            backgroundColor: 'var(--warning)',
            fontWeight: 'bold'
          }}
          outline
          onClick={() => props.handleSidebar(true, true)}
        >
          <span className="align-middle">Add</span>
        </DropdownToggle>
      </div>
      <div className="actions-right d-flex flex-wrap">
        <div className="filter-section">
          <Input
            type="text"
            style={{
              height: '75%',
              borderRadius: '5rem',
              fontSize: '1rem'
            }}
            onChange={(e) => props.handleFilter(e)}
            placeholder="Find"
            className="placeholder"
          />
        </div>
      </div>
      <div className="actions-right d-flex flex-wrap">
        <UncontrolledDropdown className="data-list-rows-dropdown d-md-block d-none">
          <DropdownToggle
            color=""
            className="sort-dropdown"
            style={{ height: '75%' }}
          >
            <span className="align-middle mx-50">Count : {props.total}</span>
          </DropdownToggle>
        </UncontrolledDropdown>
      </div>
    </div>
  )
}

class DataListConfig extends Component {
  static getDerivedStateFromProps(props, state) {
    if (
      props?.data?.length !== state.data.length ||
      state.currentPage !== props.parsedFilter.page
    ) {
      return {
        data: props?.data,
        allData: props.dataList.filteredData,
        //totalPages: props.dataList.totalPages,
        totalPages: props?.data?.length / 5,
        currentPage: parseInt(props.parsedFilter.page) - 1,
        rowsPerPage: parseInt(props.parsedFilter.perPage),
        totalRecords: props.dataList.totalRecords,
        sortIndex: props.dataList.sortIndex
      }
    }

    // Return null if the state hasn't changed
    return null
  }

  state = {
    data: [],
    totalPages: 0,
    currentPage: 0,
    columns: [
      {
        name: 'Edit',
        sortable: true,
        cell: (row) => (
          <EditComponent
            row={row}
            getSpendData={this.props.getSpendData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
          />
        )
      },
      {
        name: 'Item',
        selector: 'item',
        sortable: true,
        minWidth: '200px',
        cell: (row) => (
          <p title={row.item} className="text-truncate text-bold-500 mb-0">
            {row.item}
          </p>
        )
      },
      {
        name: 'Description',
        selector: 'desc',
        minWidth: '150px',
        sortable: true
        //cell: row => `$${row.price}`
      },
      {
        name: 'Label',
        selector: 'label',
        sortable: true
      },
      {
        name: 'Date',
        minWidth: '200px',
        selector: 'date',
        sortable: true
        // cell: row => (
        //   <Progress
        //     className="w-100 mb-0"
        //     color={row.popularity.color}
        //     value={row.popularity.popValue}
        //   />
        //)
      },
      {
        name: 'Amount',
        selector: 'amount',
        minWidth: '150px',
        sortable: true
        //cell: row => `$${row.price}`
      },

      {
        name: 'Delete',
        sortable: true,
        cell: (row) => (
          <DeleteComponent
            row={row}
            getSpendData={this.props.getSpendData}
            parsedFilter={this.props.parsedFilter}
            deleteRow={this.handleDelete}
          />
        )
      }
    ],
    mobilecolumns: [
      {
        name: 'Info',
        sortable: true,
        width: '50px',
        cell: (row) => (
          <EditComponent
            row={row}
            getData={this.props.getSpendData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
          />
        )
      },
      {
        name: 'Item',
        selector: 'item',
        sortable: true,
        minWidth: '50px',
        cell: (row) => (
          <p
            style={{ fontSize: '10px' }}
            title={row.item}
            className="text-truncate text-bold-500 mb-0"
          >
            {row.item}
          </p>
        )
      },
      {
        name: 'Amount',
        selector: 'amount',
        maxWidth: '50px',
        sortable: true,
        cell: (row) => (
          <p
            style={{ fontSize: '10px' }}
            title={row.amount}
            className="text-truncate text-bold-500 mb-0"
          >
            {row.amount}
          </p>
        )
      },
      {
        name: 'Trash',
        sortable: true,
        width: '50px',
        cell: (row) => (
          <DeleteComponent
            row={row}
            getSpendData={this.props.getSpendData}
            parsedFilter={this.props.parsedFilter}
            deleteRow={this.handleDelete}
          />
        )
      }
    ],
    allData: [],
    value: '',
    rowsPerPage: 4,
    sidebar: false,
    currentData: null,
    selected: [],
    totalRecords: 0,
    sortIndex: [],
    addNew: '',
    isLoading: false
  }

  thumbView = this.props.thumbView

  componentDidMount() {
    this.props.getSpendData(this.props.parsedFilter)
    this.props.getSpendInitialData()
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.thumbView) {
      this.thumbView = false
      let columns = [
        {
          name: 'Image',
          selector: 'img',
          minWidth: '220px',
          cell: (row) => <img src={row.img} height="100" alt={row.name} />
        },
        {
          name: 'Name',
          selector: 'name',
          sortable: true,
          minWidth: '250px',
          cell: (row) => (
            <p title={row.name} className="text-truncate text-bold-500 mb-0">
              {row.name}
            </p>
          )
        },
        {
          name: 'Category',
          selector: 'category',
          sortable: true
        },
        {
          name: 'Popularity',
          selector: 'popularity',
          sortable: true,
          cell: (row) => (
            <Progress
              className="w-100 mb-0"
              color={row.popularity.color}
              value={row.popularity.popValue}
            />
          )
        },
        {
          name: 'Order Status',
          selector: 'order_status',
          sortable: true,
          cell: (row) => (
            <Chip
              className="m-0"
              color={chipColors[row.order_status]}
              text={row.order_status}
            />
          )
        },
        {
          name: 'Price',
          selector: 'price',
          sortable: true,
          cell: (row) => `$${row.price}`
        },
        {
          name: 'Actions',
          sortable: true,
          cell: (row) => (
            <ActionsComponent
              row={row}
              getSpendData={this.props.getSpendData}
              parsedFilter={this.props.parsedFilter}
              currentData={this.handleCurrentData}
              deleteRow={this.handleDelete}
            />
          )
        }
      ]
      this.setState({ columns })
    }
  }

  handleFilter = (e) => {
    this.setState({ value: e.target.value })
    this.props.filterData(e.target.value)
  }

  handleRowsPerPage = (value) => {
    let { parsedFilter, getSpendData } = this.props
    let page = parsedFilter.page !== undefined ? parsedFilter.page : 1
    history.push(`/nominee/list?page=${page}&perPage=${value}`)
    this.setState({ rowsPerPage: value })
    getSpendData({ page: parsedFilter.page, perPage: value })
  }

  handleSidebar = (boolean, addNew = false) => {
    this.setState({ sidebar: boolean })
    if (addNew === true) this.setState({ currentData: null, addNew: true })
  }

  handleDelete = (row) => {
    this.props.deleteData(row)
    this.props.getSpendData(this.props.parsedFilter)
    if (this.state.data.length - 1 === 0) {
      let urlPrefix = this.props.thumbView
        ? '/data-list/thumb-view/'
        : '/#/nominee/list/'
      history.push(
        `${urlPrefix}list-view?page=${parseInt(
          this.props.parsedFilter.page - 1
        )}&perPage=${this.props.parsedFilter.perPage}`
      )
      this.props.getSpendData({
        page: this.props.parsedFilter.page - 1,
        perPage: this.props.parsedFilter.perPage
      })
    }
  }
  //  addData = async (obj) => {
  addData = (obj) => {
    if (
      obj.name === '' ||
      obj.relation1 === '' ||
      obj.email === '' ||
      obj.contact1 === '' ||
      obj.address === ''
    ) {
      toast.error('Please Fill All Mandatory Details')
    } else {
      this.setState({ isLoading: true })
      console.log('aaquib', obj)
      let userData = localStorage.getItem('logInUserData')
        ? JSON.parse(localStorage.getItem('logInUserData'))
        : {}
      const nominee = {
        userId: userData._id,
        item: obj.name,
        label: obj.relation1,
        date: obj.email,
        amount: obj.contact1,
        desc: obj.address
      }
      axios
        .post('/backendapi/spend/add', nominee)
        .then((res) => {
          this.setState({ isLoading: false })
          toast.success('Spend Added Successfully')
          this.handleSidebar(false, true)

          this.props.getSpendData(this.props.parsedFilter)
        })
        .catch((e) => {
          this.setState({ isLoading: false })
          toast.error('Add Spend Failed! Please contact admin')
        })

      console.log('aaquib', nominee)
    }
  }
  updateData = (obj) => {
    if (
      obj.name === '' ||
      obj.relation1 === '' ||
      obj.email === '' ||
      obj.contact1 === '' ||
      obj.address === ''
    ) {
      toast.error('Please Fill All Mandatory Details')
    } else {
      this.setState({ isLoading: true })
      console.log('aaquib', obj)
      let userData = localStorage.getItem('logInUserData')
        ? JSON.parse(localStorage.getItem('logInUserData'))
        : {}
      const nominee = {
        _id: obj._id,
        userId: userData._id,
        item: obj.name,
        label: obj.relation1,
        date: obj.email,
        amount: obj.contact1,
        desc: obj.address
      }
      axios
        .post('/backendapi/spend/update', nominee)
        .then((res) => {
          this.setState({ isLoading: false })
          toast.success('Spend Updated Successfully')
          this.handleSidebar(false, true)

          this.props.getSpendData(this.props.parsedFilter)
        })
        .catch((e) => {
          this.setState({ isLoading: false })
          toast.error('Add Nominee Failed! Please contact admin')
        })

      console.log('aaquib', nominee)
    }
  }
  handleCurrentData = (obj) => {
    obj.name = obj.item
    obj.relation1 = obj.label
    obj.relation = obj.label
    obj.email = obj.date
    obj.address = obj.desc
    obj.primaryContact = obj.amount

    this.setState({ currentData: obj })

    this.handleSidebar(true)
  }

  handlePagination = (page) => {
    let { parsedFilter, getSpendData } = this.props
    let perPage = parsedFilter.perPage !== undefined ? parsedFilter.perPage : 4
    let urlPrefix = this.props.thumbView
      ? '/#/nominee/list/'
      : '/#/nominee/list/'
    history.push(
      `${urlPrefix}list-view?page=${page.selected + 1}&perPage=${perPage}`
    )
    getSpendData({ page: page.selected + 1, perPage: perPage })
    this.setState({ currentPage: page.selected })
  }

  render() {
    let {
      columns,
      data,
      allData,
      totalPages,
      value,
      rowsPerPage,
      currentData,
      sidebar,
      totalRecords,
      sortIndex
    } = this.state
    return (
      <div
        className={`data-list ${
          this.props.thumbView ? 'thumb-view' : 'list-view'
        }`}
      >
        <DataTable
          width="200"
          columns={
            window.screen.width < 600 ? this.state.mobilecolumns : columns
          }
          data={value.length ? allData : data}
          // pagination
          // paginationServer
          // paginationComponent={() => (
          //   <ReactPaginate
          //     previousLabel={<ChevronLeft size={15} />}
          //     nextLabel={<ChevronRight size={15} />}
          //     breakLabel="..."
          //     breakClassName="break-me"
          //     pageCount={totalPages}
          //     containerClassName="vx-pagination separated-pagination pagination-end pagination-sm mb-0 mt-2"
          //     activeClassName="active"
          //     forcePage={
          //       this.props.parsedFilter.page
          //         ? parseInt(this.props.parsedFilter.page - 1)
          //         : 0
          //     }
          //     onPageChange={(page) => this.handlePagination(page)}
          //   />
          // )}
          noHeader
          subHeader
          selectableRows={window.screen.width < 600 ? false : false}
          responsive
          pointerOnHover
          selectableRowsHighlight
          onSelectedRowsChange={(data) =>
            this.setState({ selected: data.selectedRows })
          }
          customStyles={selectedStyle}
          subHeaderComponent={
            <CustomHeader
              handleSidebar={this.handleSidebar}
              handleFilter={this.handleFilter}
              handleRowsPerPage={this.handleRowsPerPage}
              rowsPerPage={rowsPerPage}
              total={totalRecords}
              index={sortIndex}
            />
          }
          sortIcon={<ChevronDown />}
          // selectableRowsComponent={Checkbox}
          // selectableRowsComponentProps={{
          //   color: 'primary',
          //   icon: <Check className="vx-icon" size={12} />,
          //   label: '',
          //   size: 'sm'
          // }}
        />
        <Sidebar
          show={sidebar}
          data={currentData}
          updateData={this.updateData}
          addData={this.addData}
          isLoading={this.state.isLoading}
          handleSidebar={this.handleSidebar}
          thumbView={this.props.thumbView}
          getSpendData={this.props.getSpendData}
          dataParams={this.props.parsedFilter}
          addNew={this.state.addNew}
        />
        <div
          className={classnames('data-list-overlay', {
            show: sidebar
          })}
          onClick={() => this.handleSidebar(false, true)}
        />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  console.log('state.dataList', state.dataList)
  const { dataList } = state
  const { data } = dataList
  data.map((data) => {
    const dateTemp = data?.date?.split('T')
    data.date = dateTemp?.[0]
    return data
  })
  console.log('state.data', data)
  return {
    dataList: state.dataList,
    data: data
  }
}

export default connect(mapStateToProps, {
  getSpendData,
  deleteData,
  updateData,
  addData,
  getSpendInitialData,
  filterData
})(DataListConfig)
